
    #headerSection{
        transition: height,background-color 700ms;

        .elementor-item{
            &:hover{
                color: #20438C!important;
            }
        }

        .elementor-sub-item{
            @media (min-width: 768px) {
                &.has-submenu{
                    .sub-arrow{
                        &:after{
                            content: ">";
                            font: 18px "Consolas", monospace;
                            font-weight: bolder;
                            right: 0px;
                            top:10px;
                            color: #20438c;
                            -webkit-transform: rotate(0deg);
                            -moz-transform: rotate(0deg);
                            -ms-transform: rotate(0deg);
                            transform: rotate(0deg);
                            padding: 0 0 2px;
                            position: absolute;
                            pointer-events: none;
                        }
                    }
                }
            }
        }
        
        .elementor-button{
            border-bottom: 2px solid #fff;
        }

        

        .hovered{
            color: #000;
        }

        .coloredLogo{
            -webkit-filter: brightness(1) invert(0) !important;
            filter: brightness(1) invert(0) !important;
        }

        .elementor-nav-menu--main{
            padding: 13px 15px!important;
        }

        .headerButton{
            font-size: 14px;
        }

        .contactUs{
            color: #20438C;
            border: 2px solid #20438C!important;
        }
    
        .rentCar{
            background-color: #20438C!important;
            color: #fff!important;
            border:2px solid #20438C!important;
        }

        .scrolledMobileNav{
            fill: #20438C!important;
        }
    }

    .otherPagesHeader{
        background-color: #fff!important;
        .elementor-button{
          border-bottom: 2px solid #20438C!important;
        }
    }

    .headerOpen{
        background-color: #fff!important;
        height: 380px;
    }

    .page-content{
        transition: margin-top 700ms;
    }

    .offsetTop{
        margin-top: 380px!important;
    }

    .scrolledNav{
        color: #000
    }

    .jumpTo{
        display: none;
    }

    .jumpto-icon .elementor-icon{
        border-bottom: 0;
    }

    .jumpToNav a{
        border-bottom: 0;
    }